<template>
  <div class="volume">
    <TimeSearch @timeChange="timeChange" :showSearch="false"></TimeSearch>
    <WholeData :nums="nums" :showBtn="true" @showModal="show = true"></WholeData>
    <div class="box">
      <div class="box-title flex ac">
        <img :src="require('@/assets/chart.png')" alt />
        新增直属人数 {{directNum}}人
      </div>
      <div class="chart">
        <ve-line
          height="100%"
          :data="directData"
          :legend-visible="false"
          :tooltip="tooltip"
          :colors="colors"
          :dataEmpty="!directData.rows.length"
          :grid="grid"
          :xAxis="xAxis"
          :yAxis="yAxis"
        ></ve-line>
      </div>
    </div>
    <div class="box">
      <div class="box-title flex ac">
        <img :src="require('@/assets/chart.png')" alt />
        新增团队人数 {{memberNum}}人
      </div>
      <div class="chart">
        <ve-line
          height="100%"
          :data="memberData"
          :legend-visible="false"
          :tooltip="tooltip"
          :colors="colors"
          :dataEmpty="!memberData.rows.length"
          :grid="grid"
          :xAxis="xAxis"
          :yAxis="yAxis"
        ></ve-line>
      </div>
    </div>
    <div class="box">
      <div class="box-title flex ac">
        <img :src="require('@/assets/chart.png')" alt />
        新增超级VIP {{vipNum}}人
      </div>
      <div class="chart">
        <ve-line
          height="100%"
          :data="vipData"
          :legend-visible="false"
          :tooltip="vipTooltip"
          :colors="colors"
          :dataEmpty="!vipData.rows.length"
          :grid="grid"
          :settings="chartSettings"
          :xAxis="xAxis"
          :yAxis="yAxis"
        ></ve-line>
      </div>
    </div>
    <van-overlay class="flex center" :show="show" @click="show = false">
      <div class="modal" @click.stop>
        <div class="title text-center">新增直属人数</div>
        <div class="content">统计新增直属人数，只显示实习团长与团长</div>
        <div class="title text-center">新增团队人数</div>
        <div class="content">统计新增的团队成员人数，只统计实习团长及团长以上等级的人数，不包括直属团长。</div>
        <div class="title text-center">新增超级VIP</div>
        <div class="content">统计新增的超级VIP，包括直属的超级VIP与团队增加的超级VIP。</div>
        <div class="btn text-center" @click="show = false">我知道了</div>
      </div>
    </van-overlay>
  </div>
</template>

<script>
import { getNewMember } from "@/api";
import TimeSearch from "@/components/TimeSearch";
import WholeData from "@/components/WholeData";
import Vue from "vue";
import { Overlay } from "vant";
import VeLine from "v-charts/lib/line.common";

Vue.use(Overlay);
Vue.component(VeLine.name, VeLine);

export default {
  components: { TimeSearch, WholeData },
  data() {
    this.chartSettings = {
      metrics: ["count"]
    };
    return {
      nums: [], // 整体数据
      show: false,
      directData: {
        columns: ["time", "count"],
        rows: []
      },
      memberData: {
        columns: ["time", "count"],
        rows: []
      },
      vipData: {
        columns: ["time", "count", "vip_to_member"],
        rows: []
      },
      tooltip: {
        show: true,
        trigger: "axis",
        formatter: data => {
          return `<div style='text-align:center'>新增${data[0].value[1]}人<br />${data[0].value[0]}</div>`;
        }
      },
      vipTooltip: {
        show: true,
        trigger: "axis",
        formatter: data => {
          const index = data[0].dataIndex;
          const num = this.vipData.rows[index].vip_to_member;
          if (num) {
            return `<div style='text-align:center'>新增${data[0].value[1]}人，${num}人已升级<br />${data[0].value[0]}</div>`;
          }
          return `<div style='text-align:center'>新增${data[0].value[1]}人<br />${data[0].value[0]}</div>`;
        }
      },
      colors: ["#E84114"],
      grid: {
        top: 15,
        bottom: 15
      },
      xAxis: {
        show: false,
        type: "category"
      },
      yAxis: {
        minInterval: 1,
        show: true,
        axisTick: {
          show: false
        },
        splitLine: {
          show: true,
          lineStyle: {
            type: "dashed"
          }
        }
      }
    };
  },
  computed: {
    directNum() {
      let num = 0;
      this.directData.rows.forEach(item => {
        num += item.count;
      });
      return num;
    },
    memberNum() {
      let num = 0;
      this.memberData.rows.forEach(item => {
        num += item.count;
      });
      return num;
    },
    vipNum() {
      let num = 0;
      this.vipData.rows.forEach(item => {
        num += item.count;
      });
      return num;
    }
  },
  methods: {
    // 时间段变化
    timeChange(params) {
      this.params = params;
      this.getData();
    },
    // 数据
    getData() {
      const params = {
        ...this.params
      };
      delete params.timeStr;
      getNewMember(params, false).then(res => {
        this.loading = false;
        if (res.success) {
          const { all, list } = res.data;
          const nums = [];
          nums.push(
            { name: "新增直属人数", value: all.direct },
            { name: "新增团队人数", value: all.member },
            { name: "新增超级VIP", value: all.vip }
          );
          this.nums = nums;
          this.list = list;
          this.directData.rows = list.map(item => ({
            time: item.time,
            count: item.direct
          }));
          this.memberData.rows = list.map(item => ({
            time: item.time,
            count: item.member
          }));
          this.vipData.rows = list.map(item => ({
            time: item.time,
            count: item.vip,
            vip_to_member: item.vip_to_member
          }));
        }
      });
    }
  }
};
</script>

<style lang="less" scoped>
.volume {
  overflow: hidden;
}

.chart {
  width: 100%;
  height: 180px;
}

.modal {
  box-sizing: border-box;
  width: 315px;
  background: rgba(255, 255, 255, 1);
  border-radius: 11px;
  padding: 30px 13px 20px;
}

.title {
  color: #282828;
  font-size: 16px;
  line-height: 23px;
  font-weight: 500;
}

.content {
  color: #666;
  font-size: 14px;
  margin: 15px 0 25px;
}

.btn {
  height: 44px;
  line-height: 44px;
  color: #fff;
  font-size: 16px;
  font-weight: 500;
  background: rgba(232, 65, 20, 1);
}

.box {
  background: rgba(255, 255, 255, 1);
  border-radius: 8px;
  width: 349px;
  box-sizing: border-box;
  margin: 0 auto 10px;
  padding: 0 10px;
}

.box-title {
  padding: 10px 0;
  color: #282828;
  font-size: 16px;
  line-height: 23px;
  img {
    width: 15px;
    margin-right: 5px;
  }
}
</style>